@charset "utf-8";
@import 'normalize.css';
@keyframes route-in {
  from {
    transform: translate3d(0, 1rem, 0);
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes route-out {
  from {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  to {
    z-index: -1;
    transform: translate3d(0, 1rem, 0);
    opacity: 0;
  }
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes avatar-in {
  0% {
    transform: scale(30);
  }
  20% {
    transform: scale(30);
  }
  100% {
    transform: scale(1);
  }
}
